export class Controller {
  /**
   * @type {import('./model').Model}
   */
  model = null;
  /**
   * @type {import('./view').View}
   */
  view = null;

  /**
   *
   * @param {import('./model').Model} model
   * @param {import('./view').View} view
   */
  constructor(model, view) {
    this.model = model;
    this.view = view;
    this.createViewCallbacks();
    this.createEventListeners();
  }

  //	Callbacks
  createViewCallbacks() {
    const self = this;
    this.view.onAddItemToCart = function (product, input, messageContainer) {
      var quantity = input.value;
      if (isNaN(quantity) || !quantity || quantity <= 0) {
        // show error message
        self.view.flashIndicator(messageContainer, "Invalid quantity selected.");
        return;
      }
      var added = self.model.addToCart(product, quantity);
      if (added) {
        self.view.setCart(self.model.getCartData());
        self.view.flashIndicator(messageContainer);
        self.view.resetQuantity(input);
        self.view.openCart();
        //	last item notification
      }
      //	TODO: If not able to add product, do some error logging here or something
    };

    this.view.onBibleDropdownChange = function (newIndex, value) {
      self.model.selectedStockNum = value;
      self.view.setProducts(self.model.keys, self.model.products, self.model.getSelectedBible(), self.model.fullMP3s);
    };

    this.view.onSearchbarValueChange = function (key, value) {
      self.view.setLanguageAutocomplete(key, value, self.model.langaugeNameList);
    };

    this.view.onLanguageChange = function (language) {
      self.view.languageAutocomplete.style.display = "none";
      self.handleLanguageChange(language, false);
    };

    this.view.onUpdateCartItemQuantity = function (item, element) {
      const newQuantity = parseInt(element.value, 10);
      if (isNaN(newQuantity) || !newQuantity || newQuantity <= 0) {
        throw new Error("Bad quantity");
      }
      self.model.updateCart(item, newQuantity);
      self.view.setCart(self.model.getCartData());
    };

    this.view.onDeleteCartItem = function (item) {
      self.model.deleteCartItem(item);
      self.view.setCart(self.model.getCartData());
    };

    this.view.onCheckout = function () {
      if (self.model.getCartData().total_quantity > 0) {
        if (window.firebase.auth().currentUser) {
          window.location.href = "/audio-bibles/checkout";
        } else {
          self.view.login.style.display = "block";
        }
      }
    };

    this.view.onGuestCheckout = function () {
      if (self.model.getCartData().total_quantity > 0) {
        window.location.href = "/checkout1";
      }
    };

    this.view.cartCheckoutBtn.addEventListener("click", (ev) => {
      ev.preventDefault();
      ev.stopPropagation();
      if (this.model.user) {
        window.location.href = "/checkout1";
        return;
      }
      this.view.openSigningModal();
    });

    this.view.onLogout = function () {
      window.firebase
        .auth()
        .signOut()
        .then(
          function () {
            self.view.setLoadingScreen("Logging out...");
            window.location.replace(window.location.href); // refresh browser
          },
          function (error) {
            console.log("error occurred with logout");
          }
        );
    };
  }

  createEventListeners() {
    //	Event listeners
    this.onViewDidLoad();

    document.body.addEventListener("click", (e) => {
      //	closes language autcomplete when background is clicked
      if (this.view.languageAutocomplete.style.display === "block" && !e.target.closest("#language-search-dropdown")) {
        this.view.languageAutocomplete.style.display = "none";
      }
    });

    //	Firebase event listener
    //	Fires when the state of the user changes.
    window.firebase.auth().onAuthStateChanged((user) => {
      this.view.setUserGreeting(user);
      this.model.user = user;
    });
  }

  onViewDidLoad() {
    console.log("loaded");
    var self = this;

    //	TODO: Check ie version
    if (self.model.browserNotCompatible()) {
      self.view.setLoadingScreen(
        "Your browser is not compatible. We recommend using Google Chrome, Mozilla Firefox, or Safari."
      );
      return;
    }

    //self.model.saveCart([]);	// clears cart (only during beta testing)

    self.view.setLoader("show");
    self.view.setFirebaseAuthLoginUI(self.model.firebaseAuthUIConfig);
    self.model
      .fetchProducts()
      .then(function () {
        self.model
          .fetchBibles()
          .then(function () {
            var params = self.parseURLParameters(self.model.selectedLanguage, self.model.selectedStockNum);
            self.view.setCart(self.model.getCartData());
            self.view.setLoader("hide");
            document.body.style.overflow = "hidden"; // temporarily stop scrolling to stop wierd jumpy effect
            self.handleLanguageChange(params.language, params.stockNum);
            document.body.style.overflow = "auto"; // enable scrolling again
          })
          .catch(function (error) {
            console.log(error);
          });
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  parseURLParameters(lang, stockId) {
    return { language: lang, stockNum: stockId };
  }

  handleLanguageChange(lang, stockNum) {
    var self = this;
    self.model.selectedLanguage = lang; // set language
    self.model.selectedStockNum = stockNum; // reset stock num
    self.view.setSearchbar(self.model.selectedLanguage);
    self.view.setBiblesDropdown(self.model.getBiblesForSelectedLanguage(), self.model.getSelectedBible());
    self.view.setProducts(self.model.keys, self.model.products, self.model.getSelectedBible(), self.model.fullMP3s);
  }
}
