export class View {
  cartWidth = "300px";
  searchbar = document.getElementById("searchbar");
  bibleDropdown = document.getElementById("Version");
  productList = document.querySelector("#products-container");
  emptyProducts = document.querySelector(".w-dyn-empty");
  languageAutocomplete = document.getElementById("language-search-dropdown");
  cart = document.querySelector(".cart-item-list");
  emptyCart = document.querySelector(".empty-state");
  cartContainer = document.querySelector(".cart-container");
  closeLink = document.querySelector(".cart-close");
  cartIcon = document.querySelector("a.store-cart-btn");
  cartBadge = document.querySelector(".store-cart-item-number");
  greeting = document.querySelector(".store-sign-in > div");
  firebaseAuthContainer = document.querySelector("#sign-in-content");
  checkoutAsGuestButton = document.querySelector("#checkout-as-guest");
  cartCheckoutBtn = document.querySelector(".cart-btn-wrapper");
  //	Callbacks
  onAddItemToCart;
  onBibleDropdownChange;
  onSearchbarValueChange;
  onLanguageChange;
  onEditCartItemQuantity;
  onUpdateCartItemQuantity;
  oDeleteCartItem;
  onCheckout;
  onLogout;
  onSignIn;

  constructor() {
    this.createCallbacks();
    this.checkoutAsGuestButton.textContent = "Checkout as guest";
  }

  createCallbacks() {
    this.closeLink.style.opacity = "1";
    this.cartContainer.addEventListener("click", (ev) => {
      ev.preventDefault();
      ev.stopPropagation();
    });
    this.bibleDropdown.onchange = ({ target: { options, selectedIndex } }) => {
      var value = options[selectedIndex].value;
      this.onBibleDropdownChange(selectedIndex, value);
    };

    this.searchbar.onclick = this.searchbar.onkeyup = (e) => {
      this.onSearchbarValueChange(e.which, e.target.value);
    };

    this.searchbar.addEventListener("keyup", (ev) => this.handleLanguageAutocompleteInteraction(ev));
    this.languageAutocomplete.addEventListener("mouseover", (ev) => this.handleLanguageAutocompleteInteraction(ev));

    this.searchbar.addEventListener("keydown", function (e) {
      if (e.which == 40 || e.which == 38 || e.which == 13) {
        e.preventDefault(); // stops left and right cursor movement
      }
    });

    var checkouts = document.querySelectorAll(".checkout");
    for (var i = 0; i < checkouts.length; i++) {
      checkouts[i].onclick = () => {
        this.onCheckout();
      };
    }

    this.checkoutAsGuestButton.onclick = () => {
      this.onGuestCheckout();
    };

    this.closeLink.addEventListener("click", () => {
      this.closeCart();
    });
  }

  setSearchbar(text) {
    this.searchbar.value = text;
  }
  setBiblesDropdown(bibles, selectedBible) {
    var self = this;
    var dropdown = self.bibleDropdown;

    //	reset dropdown
    dropdown.innerHTML = "";

    if (!bibles || bibles.length === 0) {
      dropdown.innerHTML = "<option selected>No Versions Available</option>";
      return;
    }

    for (var j = 0; j < bibles.length; j++) {
      var bible = bibles[j];

      //	Determine if old testament, new, or portions
      var portion = "";
      var dam_id = bible.NT_Audio_DAM_ID || bible.OT_Audio_DAM_ID || "";
      if (dam_id) {
        var a = dam_id.slice(6, 7).toLowerCase();
        if (a === "n") {
          portion = " - New Testament";
        } else if (a === "o") {
          portion = " - Old Testament";
        } else if (a === "p") {
          portion = " - Portions";
        }
      }

      var selected = "";
      if (!selectedBible && j === 0) {
        selected = "selected";
      } else if (selectedBible && selectedBible.StockNumber.toLowerCase() === bible.StockNumber.toLowerCase()) {
        selected = "selected";
      }

      dropdown.innerHTML +=
        "<option " +
          selected +
          " value='" +
          bible.StockNumber +
          "'>" +
          bible.Volume_Name +
          ", " +
          bible.media_type.capitalize() +
          " - " +
          bible.Portion || bible.Scope + "</option>";
    }
  }

  /**
   * Checks product sale avaliability
   * @param {*} product
   * @param {*} keys
   * @param {*} bible
   */
  isProductAvailable(product, keys, bible) {
    const productName = String(product.title).toLowerCase();
    if (keys.proclaimers.indexOf(productName) >= 0 && bible.Proclaimer === "Yes") {
      return true;
    } else if (keys.biblesticks.indexOf(productName) >= 0 && bible.Digital_Player === "Yes") {
      return true;
    } else if (productName.includes("sd card") && bible.Micro_SD === "Yes") {
      return true;
    } else if (productName.includes("mp3 disc") && bible.MPs) {
      return true;
    }
    return false;
  }

  createProductPriceHtml(product, bible, fullMP3s) {
    const productName = product.title.toLowerCase();
    const pricing = (productName === "mp3 disc") & (fullMP3s.indexOf(bible.StockNumber) >= 0) ? "29.00" : product.price;
    return `
			<h4 class="txt-h3 txt-deep-red txt-no-margin">${pricing} USD</h4>
			<div>
				<form
					class="store-item__content-options"
        >
          <input 
          type="number" 
          class="default-input default-input--store-qty w-input" 
          maxlength="256" 
          name="Product-Quantity" 
          data-name="Product Quantity" 
          placeholder="" 
          value="1"
          id="quantity-${product.id}">
					<input
					type="submit"
					data-loading-text="Adding to cart..."
					value="Add to Cart"
					data-id="${product.id}"
					class="btn-md btn-md-no-margin-y btn-md--store w-button"
					/>
				</form>
				<div style="display: none;" class="w-commerce-commerceaddtocartoutofstock store-item--no-stock">
					<div class="txt-md">This product is out of stock.</div>
				</div>
				<div
					data-node-type="commerce-add-to-cart-error"
					style="display: none;"
					id="message-container-${product.id}"
					class="w-commerce-commerceaddtocarterror store-item--not-available"
				>
					<div class="txt-brand-red .w-add-to-cart-error-msg">
						Product is not available in this quantity.
					</div>
				</div>
			</div>
		`;
  }

  setProducts(keys, products, bible, fullMP3s) {
    this.productList.innerHTML = "";

    fullMP3s = fullMP3s || [];

    if (!bible) {
      return;
    } // rare case - more of sanity check
    const avaliableProducts = products.filter((product) => this.isProductAvailable(product, keys, bible));

    if (avaliableProducts.length === 0) {
      if (this.emptyProducts) {
        this.emptyProducts.style.display = "block";
      }
      this.productList.style.display = "none";
    } else {
      this.productList.style.display = "grid";
      if (this.emptyProducts) {
        this.emptyProducts.style.display = "none";
      }
    }

    const productNodes = avaliableProducts.map((product) => {
      const node = document.createElement("div");
      node.setAttribute("role", "listitem");
      node.classList.add("col-wrapper--store");
      node.dataset.id = product.id;

      const callToOrderHTML = '<div class="txt-sm txt-deep-red">*Call to Order</div>';
      const productPriceHTML = this.createProductPriceHtml(product, bible, fullMP3s);
      const html = `
            <img src="https://cdn.faithcomesbyhearing.com/resources/fcbh/img/store/products/${
              product.thumbnail
            }" alt="${product.title}" loading="lazy" class="store-item__image" />
            <div class="store-item__content">
              <div class="txt-post-title-wrapper pb-8 mb-16 store__item-title">
                <h3 class="txt-h3">${product.title}</h3>
              </div>
      <div class="txt-md mb-16">${product.description}</div>
      ${product.call_for_pricing ? callToOrderHTML : productPriceHTML}
            </div>`;
      node.innerHTML = html;
      return node;
    });

    this.productList.append(...productNodes);
    //	seperate loop for adding event listener
    //	creating html and adding callbacks don't work well together
    const productForms = this.productList.querySelectorAll("form");

    for (const form of productForms) {
      const input = form.querySelector('input[type="number"]');
      const button = form.querySelector('input[type="submit"');
      const product = products.find(({ id }) => id === button?.dataset.id);
      if (!product) {
        break;
      }
      const messageContainer = document.getElementById(`message-container-${product.id}`);
      const submitFn = (ev) => {
        try {
          ev.preventDefault();
          this.onAddItemToCart(product, input, messageContainer);
        } catch (err) {
          console.error(err);
        }
      };
      form.addEventListener("submit", submitFn);
      button.addEventListener("click", submitFn);
    }
  }

  setLanguageAutocomplete(key, value, languageList) {
    var _this = this;

    if (key !== 38 && key !== 40 && key !== 13) {
      var matches;
      var i;

      var _ret = (function () {
        var self = _this;
        var div = self.languageAutocomplete;
        div.innerHTML = "";
        div.style.display = "none";

        var search = value.toLowerCase();
        matches = 0;

        if (search.length > 0) {
          div.style.display = "block";

          var _loop = function _loop() {
            if (matches > 7) {
              return {
                v: {
                  v: void 0,
                },
              };
            }
            var languageName = languageList[i];
            if (languageName.toLowerCase().search(search) == 0) {
              matches++;
              var matchedText = languageName.slice(0, search.length);
              var restOfText = languageName.slice(search.length);
              var d = document.createElement("div");
              d.innerHTML = "<strong>" + matchedText + "</strong>" + restOfText;
              d.onclick = function () {
                self.onLanguageChange(languageName);
              };
              div.appendChild(d);
            }
          };

          for (i = 0; i < languageList.length; i++) {
            var _ret2 = _loop();

            if (typeof _ret2 === "object") return _ret2.v;
          }

          if (matches == 0) {
            div.style.display = "none";
            console.log("no matches");
          }
        }
      })();

      if (typeof _ret === "object") return _ret.v;
    }
  }
  setCart(data) {
    var self = this;

    this.cart.innerHTML = "";

    if (data.items.length > 0) {
      document.querySelector(".default-state").style.display = "block";
      this.emptyCart.style.display = "none";
    } else {
      document.querySelector(".default-state").style.display = "none";
      this.emptyCart.style.display = "flex";
    }

    const items = data.items.map((item) => {
      const node = document.createElement("div");
      node.classList.add("cart-item");
      node.dataset.id = item.id;
      node.innerHTML = `
			<img src="https://cdn.faithcomesbyhearing.com/resources/fcbh/img/store/products/${item.thumbnail}" alt="${item.title}" class="cart-item-image">
			<div class="cart-item-info">
				<div class="cart-item-title"> ${item.title}</div>
				<div class="txt-md cart-item-price">$&nbsp;${item.unit_price}&nbsp;USD</div>
				<a href="#" class="btn-link w-button">
					<div>Remove</div>
				</a>
			</div>
			<input type="text" required="" pattern="[0-9]+" class="cart-item-qty default-input w-input" name="cart-item-qty" min="0" value="${item.quantity}">
		  `;
      return node;
    });

    this.cart.append(...items);
    //	add event listeners
    const nodes = this.cart.querySelectorAll(".cart-item");
    for (const node of nodes) {
      const item = data.items.find((it) => it.id === node.dataset.id);
      if (!item) {
        //Sanity Check
        break;
      }
      const removeBtn = node.querySelector("a");
      removeBtn.addEventListener("click", () => {
        this.onDeleteCartItem(item);
      });

      const qtyInput = node.querySelector("input");
      qtyInput.addEventListener("change", () => {
        try {
          this.onUpdateCartItemQuantity(item, qtyInput);
        } catch {
          qtyInput.value = item.quantity; // If something went wrong return to previous qty
        }
      });
    }
    // Show the cart total
    const total = this.cartContainer.querySelector(".cart-subtotal");
    total.textContent = `$ ${data.total.toFixed(2)} USD`;

    this.cartBadge.style.display = data.total_quantity ? "inline-block" : "none";
    this.cartBadge.innerHTML = data.total_quantity;
  }
  setFirebaseAuthLoginUI(config) {
    var self = this;
    var ui = new window.firebaseui.auth.AuthUI(window.firebase.auth());
    ui.start(self.firebaseAuthContainer, config);
  }
  setLoader(mode) {
    if (mode === "show") {
      var loader = document.createElement("div");
      loader.id = "spinner-container";
      loader.innerHTML =
        "<div class='spinner'><div class='bounce1'></div><div class='bounce2'></div><div class='bounce3'></div></div>";
      this.productList.appendChild(loader);

      setTimeout(function () {
        loader.style.opacity = 1;
      }, 100);
    } else {
      document.getElementById("spinner-container").style.opacity = 0;
      setTimeout(function () {
        var loader = document.getElementById("spinner-container");
        if (loader) {
          loader.parentElement.removeChild(loader);
        }
      }, 100);
    }
  }
  flashIndicator(el, msg) {
    var indicator = el.firstChild;
    indicator.innerHTML = msg ? msg : "Added to cart!";
    el.style.display = "block";
    setTimeout(function () {
      el.style.display = "none";
    }, 2000);
  }
  handleLanguageAutocompleteInteraction(e) {
    var self = this;
    var div = self.languageAutocomplete;
    var active = div.querySelector(".isActive");

    if (div.style.display === "block") {
      if (e.type === "mouseover" && e.target.nodeName.toLowerCase() != "strong") {
        if (active) {
          active.classList.remove("isActive");
        }
        active = e.target;
        active.classList.add("isActive");
      } else if (e.type == "keyup" && (e.which == 40 || e.which == 38)) {
        e.preventDefault();

        if (active) {
          active.classList.remove("isActive");
        }

        if (e.which == 40) {
          //	down
          active = !active ? div.firstChild : active.nextElementSibling || active;
        } else if (e.which == 38) {
          // up
          if (!active) {
            return;
          }
          active = active.previousElementSibling || active;
        }
        active.classList.add("isActive");
        self.setSearchbar(active.innerText);
      } else if (e.type == "keyup" && e.which == 13) {
        //	enter was pressed
        if (active) {
          self.onLanguageChange(active.innerText);
        }
      }
    }
  }
  resetQuantity(el) {
    el.value = 1;
  }
  openCart() {
    this.cartIcon.dispatchEvent(
      new MouseEvent("click", {
        view: window,
        bubbles: true,
        cancelable: false,
      })
    );
  }
  closeCart() {
    document.querySelector(".cart-wrapper").dispatchEvent(
      new MouseEvent("click", {
        view: window,
        bubbles: true,
        cancelable: false,
      })
    );
  }
  openEditBox(element) {
    var div = element.querySelector(".content-block > div:nth-of-type(3)");
    var isClosed = div.style.maxHeight == "0px" || div.style.maxHeight == "0";
    div.style.padding = isClosed ? "8px 5px" : "0px";
    div.style.maxHeight = isClosed ? div.scrollHeight + "px" : "0px";
    div.style.border = isClosed ? "1px #e2e2e2 solid" : "none";
  }
  setLastItemAddedBanner(product, data) {
    var self = this;
    var el = document.getElementById("last-item-added");
    if (!el) {
      var parent = document.getElementById("store-wrapper");
      var sibling = document.getElementById("cart-sign-in-div");
      var div = document.createElement("div");
      div.id = "last-item-added";
      parent.insertBefore(div, sibling);
    }

    var pluralExtension = data.total_quantity > 1 ? "s" : "";
    el = document.getElementById("last-item-added");
    el.innerHTML =
      "<div class='added-div'><span></span><img src='https://cdn.faithcomesbyhearing.com/resources/fcbh/img/store/products/" +
      product.thumbnail +
      "' style='margin:0px 10px'/><span>Added to Cart</span></div><div class='subtotal-div'><p>Cart subtotal (" +
      data.total_quantity +
      " item" +
      pluralExtension +
      "):<span> $" +
      data.total.toFixed(2) +
      "</span></p><section id='proceed-to-checkout' class='hover' onclick='onCheckout()'>Proceed to checkout</section><span class='hover last-item-cart-button'>Cart</span><div style='clear:both'></div></div><div style='clear:both'></div>";

    el.querySelector(".last-item-cart-button").onclick = function () {
      self.openCart();
    };

    el.querySelector("#proceed-to-checkout").onclick = function () {
      self.onCheckout();
    };
  }
  setUserGreeting(user) {
    var self = this;
    if (user) {
      // User is signed in.
      console.log("signed in");
      // hide modal
      document.querySelector(".modal-overlay").dispatchEvent(
        new MouseEvent("click", {
          view: window,
          bubbles: true,
          cancelable: false,
        })
      );
      self.greeting.innerHTML =
        "Welcome<span class='user-name'>, " +
        user.displayName +
        "</span> (<span style='text-decoration:underline' class='hover logout'>Logout</span>)";
      self.greeting.querySelector(".logout").onclick = function (ev) {
        ev.preventDefault();
        ev.stopPropagation();
        self.onLogout();
      };
    } else {
      // No user is signed in.
      console.log("signed out");
      self.greeting.innerHTML = "<span id='sign-in'>Sign In</span>";
    }
  }
  setLoadingScreen(message) {
    var div = document.createElement("div");
    div.id = "loading-screen";
    div.innerHTML =
      "<h2 style='font-family:Raleway, sans-serif;text-align:center;margin-top: 100px;width:100%'>" + message + "</h2>";
    document.body.appendChild(div);
  }
  hideLastItemAddedBanner() {
    var el = document.getElementById("last-item-added");
    if (el) {
      el.style.display = "none";
    }
  }

  openSigningModal() {
    this.greeting.dispatchEvent(
      new MouseEvent("click", {
        view: window,
        bubbles: true,
        cancelable: false,
      })
    );
  }
}
