export class Model {
  minimumIEVersion = 11;
  products = [];
  bibles = [];
  langaugeNameList = [];
  damIdStockNumberMap = {};
  keys = {
    proclaimers: ["proclaimer (mid-sized)", "proclaimer (micro)", "proclaimer"],
    biblesticks: ["military biblestick", "biblestick"],
  };
  selectedLanguage = "English"; // default value
  selectedStockNum = "C2CEV"; // default value

  //	Stock number for languages with $29 MP3's (as opposed to $10)
  fullMP3s = ["C1ESV", "C2ESV", "C1KJV", "C2KJV", "C1NRS", "C2NRS", "C2CEV", "C1CEV"];

  localStorageId = "fcbh_cart";

  firebaseAuthUIConfig = {
    callbacks: {
      signInSuccessWithAuthResult: function signInSuccessWithAuthResult(authResult, redirectUrl) {
        // User successfully signed in.
        // Return type determines whether we continue the redirect automatically
        // or whether we leave that to developer to handle.
        return true;
      },
      signInSuccess: function signInSuccess() {
        return false;
      },
    },
    // Will use popup for IDP Providers sign-in flow instead of the default, redirect.
    signInFlow: "popup", // popup
    signInSuccessUrl: "", // aribtrary url goes in here
    signInOptions: [
      // Leave the lines as is for the providers you want to offer your users.
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      firebase.auth.FacebookAuthProvider.PROVIDER_ID,
      firebase.auth.EmailAuthProvider.PROVIDER_ID,
    ],
    // Terms of service url.
    tosUrl: "<your-tos-url>",
  };
  DB = null;
  user = null;

  constructor() {
    // Initialize Firebase project w/ products
    const productsFirebase = window.firebase.initializeApp(
      {
        apiKey: "AIzaSyDTePGR2QMyGNEZDv8EuZ1JAkN0pbRWVSs",
        authDomain: "fcbh-store.firebaseapp.com",
        databaseURL: "https://fcbh-store.firebaseio.com",
        projectId: "fcbh-store",
        storageBucket: "fcbh-store.appspot.com",
        messagingSenderId: "398564581481",
      },
      "secondary"
    );

    this.DB = productsFirebase.firestore();
  }

  fetchProducts() {
    var self = this;
    return new Promise(function (resolve, reject) {
      self.DB.collection("products")
        .orderBy("index")
        .get()
        .then(function (querySnapshot) {
          querySnapshot.forEach(function (doc) {
            var product = doc.data();
            product.id = doc.id;
            self.products.push(product);
          });
          resolve();
        })
        .catch(function (error) {
          console.log("firestore failed");
          reject(error);
        });
    });
  }
  fetchBibles() {
    var self = this;
    var url = `${FCBH.BASE_URL}resources/store.json`;
    return new Promise(function (resolve, reject) {
      window.$.get(
        url,
        function (data, status) {
          if (status == "success" && data && data.length > 0) {
            self.parseRawBibles(data);
            console.log(data);
            resolve();
          } else {
            console.log("fetch bibles status wasn't success");
            reject(false);
          }
        },
        "json"
      ).fail(function (error) {
        console.log("fetch bibles failed");
        reject(error);
      });
    });
  }
  parseRawBibles(rawBibles) {
    var self = this;
    var keys = ["Full_Bible_Audio_DAM_ID", "OT_Audio_DAM_ID", "NT_Audio_DAM_ID"];

    for (var i = 0; i < rawBibles.length; i++) {
      var row = rawBibles[i].bible;
      if (row.Volume_Name !== "#N/A" && row.FCBHStore === "Licensed") {
        var damId = row.Full_Bible_Audio_DAM_ID || row.OT_Audio_DAM_ID || row.NT_Audio_DAM_ID;
        if (damId && damId !== "N/A") {
          self.bibles.push(row);

          var languageName = row.LangName;
          if (self.langaugeNameList.indexOf(languageName) == -1) {
            self.langaugeNameList.push(languageName);
          }

          self.damIdStockNumberMap[damId.toLowerCase()] = row.StockNumber.toLowerCase();
        }
      }
    }
  }
  setSelectedLanguage(lang) {
    this.selectedLanguage = lang;
  }
  getBiblesForSelectedLanguage() {
    var self = this;
    var bibles = [];
    for (var j = 0; j < self.bibles.length; j++) {
      var bible = self.bibles[j];
      if (bible.LangName.toLowerCase() === self.selectedLanguage.toLowerCase()) {
        bibles.push(bible);
      }
    }
    return bibles;
  }

  getSelectedBible() {
    var self = this;
    var lang = self.selectedLanguage;
    var stockNum = self.selectedStockNum;

    var backupBible;
    for (var i = 0; i < self.bibles.length; i++) {
      var bible = self.bibles[i];
      if (bible.LangName.toLowerCase() === lang.toLowerCase() && !backupBible) {
        backupBible = bible;
      }

      if (stockNum && bible.StockNumber === stockNum) {
        return bible;
      }
    }
    return backupBible;
  }

  /**
   * return a boolean value stating whether item was added
   * if it was added, display indicator "added to cart"
   */
  addToCart(product, quantity) {
    var self = this;
    var bible = self.getSelectedBible();
    if (!bible) {
      return false;
    }

    var productAlreadyInCart = false;
    var cart = self.getCart();
    var price =
      (product.title === "MP3 Disc") & (self.fullMP3s.indexOf(bible.StockNumber) >= 0) ? "29.00" : product.price;

    for (var i = 0; i < cart.length; i++) {
      var item = cart[i];

      if (item.stock_no === bible.StockNumber && item.id === product.id) {
        item.quantity = parseInt(item.quantity) + parseInt(quantity);
        item.subtotal = parseFloat(price) * parseInt(item.quantity);
        item.last_addition = new Date().getTime();
        productAlreadyInCart = true;
        break;
      }
    }

    if (!productAlreadyInCart) {
      cart.push({
        id: product.id,
        title: product.title,
        unit_price: price,
        stock_no: bible.StockNumber,
        language: bible.LangName,
        version: bible.Volume_Name,
        media_type: bible.media_type.capitalize(),
        portion: bible.Portion,
        quantity: quantity,
        thumbnail: product.thumbnail,
        subtotal: parseFloat(price) * parseInt(quantity),
        last_addition: new Date().getTime(),
      });
    }

    self.saveCart(cart);
    return true;
  }
  updateCart(item, quantity) {
    var items = this.getCart();
    for (var i = 0; i < items.length; i++) {
      if (items[i].stock_no === item.stock_no && items[i].id === item.id) {
        items[i].quantity = quantity;
        items[i].subtotal = parseFloat(items[i].unit_price) * parseInt(quantity);
        break;
      }
    }
    this.saveCart(items);
  }
  deleteCartItem(item) {
    var items = this.getCart();
    for (var i = 0; i < items.length; i++) {
      if (items[i].id === item.id && items[i].stock_no === item.stock_no) {
        items.splice(i, 1);
        break;
      }
    }
    this.saveCart(items);
  }
  getCart() {
    var c = localStorage.getItem(this.localStorageId);
    return c ? JSON.parse(c) : [];
  }
  saveCart(newCart) {
    localStorage.setItem(this.localStorageId, JSON.stringify(newCart));
  }
  /**
   * returns cart items along with totals
   */
  getCartData() {
    var self = this;
    var items = self.getCart();

    var runningTotal = 0;
    var runningQuantity = 0;
    for (var i = 0; i < items.length; i++) {
      runningTotal += parseFloat(items[i].subtotal);
      runningQuantity += parseInt(items[i].quantity);
    }

    var obj = {
      items: items,
      subtotal: runningTotal,
      total: runningTotal,
      total_quantity: runningQuantity,
    };
    return obj;
  }
  browserNotCompatible() {
    var version = this.minimumIEVersion;
    var ua = window.navigator.userAgent;
    var msie = ua.indexOf("MSIE ");

    if (msie > 0) {
      // 				return true;
      const e = parseInt(ua.substring(msie + 5, ua.indexOf(".", msie)), 10);
      return e < version;
    }

    var trident = ua.indexOf("Trident/");
    if (trident > 0) {
      // IE 11 => return version number
      var rv = ua.indexOf("rv:");
      const e = parseInt(ua.substring(rv + 3, ua.indexOf(".", rv)), 10);
      return e < version;
    }
    return false;
  }
}
