import { Model } from "../util/store/model";
import { View } from "../util/store/view";
import { Controller } from "../util/store/controller";

String.prototype.capitalize = function () {
  var f = function f(s) {
    s.charAt(0).toUpperCase() + s.substring(1);
  };
  return (
    this.toLowerCase()
      .split("-")
      //.map(f(s))
      .map(function (s) {
        return s.charAt(0).toUpperCase() + s.substring(1);
      })
      .join("-")
  );
};

function createScript(src) {
  return new Promise((resolve) => {
    const script = document.createElement("script");
    script.type = "text/javascript";
    script.setAttribute("defer", "");
    script.src = src;
    document.head.appendChild(script);
    script.onload = function () {
      resolve();
    };
  });
}

window.addEventListener("load", async () => {
  await createScript("https://www.gstatic.com/firebasejs/4.9.1/firebase-app.js");
  await createScript("https://www.gstatic.com/firebasejs/4.9.1/firebase-firestore.js");
  await createScript("https://cdn.firebase.com/libs/firebaseui/2.5.1/firebaseui.js");
  await createScript("https://www.gstatic.com/firebasejs/4.9.1/firebase-auth.js");
  FCBH.initializeFirebase();
  const model = new Model();
  const view = new View();
  const controller = new Controller(model, view);
});
